<template>
  <div id="dod-form">
    <form @submit.prevent="handleSubmit">
      <label>Search dod</label>
      <input v-model="dod.question" type="text" />
      <button>Ask Question</button>
    </form>
  </div>
</template>

<script>
  export default {
    name: 'dod-form',
    data() {
      return {
        dod: {
          question: '',
          answer: '',
        },
      }
    },
    methods: {
      handleSubmit() {
        this.$emit('ask:question', this.dod)
      },
    },
  }
</script>

<style scoped>
  form {
    margin-bottom: 2rem;
  }
</style>