<template>
  <div id="dod-table">
    <button v-on:click="toggleShow">{{ toggle_status }}</button>
    <div class="body" v-show="showSection">
    <table>
      <tbody>
        <tr v-for="dod in dods" :key="dod.id">
          <td>{{ dod.id }}</td>
          <td>{{ dod.question }}</td>
          <td>{{ dod.answer }}</td>
        </tr>
      </tbody>
    </table>
    </div>
  </div>
</template>

<script>
export default {
  name: 'dod-table',
  props: {
    dods: Array,
  },
  data() {
      return {
          showSection: false,
          toggle_status: "Expand History"
      }
  },
  methods: {
      toggleShow() {
          this.showSection = !this.showSection
          if(!this.showSection) {
              this.toggle_status = "Expand History"
          }
          else {
              this.toggle_status = "Collapse History"
          }
      }
  }
}
</script>